export default "import React from 'react';\r\nimport Grid from '@material-ui/core/Grid';\r\nimport Button from '@material-ui/core/Button';\r\nimport ButtonGroup from '@material-ui/core/ButtonGroup';\r\nimport Box from '@material-ui/core/Box';\r\n\r\nexport default function GroupedButtons() {\r\n  return (\r\n    <Grid container spacing={3}>\r\n      <Grid item xs={12} md={6}>\r\n        <Grid container spacing={1} direction='column' alignItems='center'>\r\n          <Grid item>\r\n            <ButtonGroup size='small' aria-label='small outlined button group'>\r\n              <Button>One</Button>\r\n              <Button>Two</Button>\r\n              <Button>Three</Button>\r\n            </ButtonGroup>\r\n          </Grid>\r\n          <Grid item>\r\n            <ButtonGroup\r\n              color='primary'\r\n              aria-label='outlined primary button group'>\r\n              <Button>One</Button>\r\n              <Button>Two</Button>\r\n              <Button>Three</Button>\r\n            </ButtonGroup>\r\n          </Grid>\r\n          <Grid item>\r\n            <ButtonGroup\r\n              color='secondary'\r\n              size='large'\r\n              aria-label='large outlined secondary button group'>\r\n              <Button>One</Button>\r\n              <Button>Two</Button>\r\n              <Button>Three</Button>\r\n            </ButtonGroup>\r\n          </Grid>\r\n        </Grid>\r\n      </Grid>\r\n      <Grid item xs={12} md={6}>\r\n        <Grid container spacing={1} direction='column' alignItems='center'>\r\n          <Grid item>\r\n            <Box boxShadow={0} clone>\r\n              <ButtonGroup\r\n                variant='contained'\r\n                size='small'\r\n                aria-label='small contained button group'>\r\n                <Button>One</Button>\r\n                <Button>Two</Button>\r\n                <Button>Three</Button>\r\n              </ButtonGroup>\r\n            </Box>\r\n          </Grid>\r\n          <Grid item>\r\n            <Box boxShadow={0} clone>\r\n              <ButtonGroup\r\n                variant='contained'\r\n                color='primary'\r\n                aria-label='full-width contained primary button group'>\r\n                <Button>One</Button>\r\n                <Button>Two</Button>\r\n                <Button>Three</Button>\r\n              </ButtonGroup>\r\n            </Box>\r\n          </Grid>\r\n          <Grid item>\r\n            <Box boxShadow={0} clone>\r\n              <ButtonGroup\r\n                variant='contained'\r\n                color='secondary'\r\n                size='large'\r\n                aria-label='large contained secondary button group'>\r\n                <Button>One</Button>\r\n                <Button>Two</Button>\r\n                <Button>Three</Button>\r\n              </ButtonGroup>\r\n            </Box>\r\n          </Grid>\r\n        </Grid>\r\n      </Grid>\r\n      <Grid item xs={12}>\r\n        <ButtonGroup fullWidth aria-label='full width outlined button group'>\r\n          <Button>Full</Button>\r\n          <Button>width</Button>\r\n          <Button>ButtonGroup</Button>\r\n        </ButtonGroup>\r\n      </Grid>\r\n    </Grid>\r\n  );\r\n}\r\n";